@media (max-width: 575px) {
  .input-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  .input-container > i {
    font-size: 18px;
    margin-top: 5px;
    margin-bottom: 10px;
    transform: rotate(90deg);
  }

  .input-container label{
      width: 90%;
      display: inline-block;
  }

  .input-container label i{
      position: relative;
      top: 25px;
      right: 15px;
      display: block;
      color: gray;
  }

  input {
    width: 100%;
  }

  body {
    padding: 3%;
